#system {
  padding: 30px 0;
}
#system .container {
  min-height: 84vh;
  background-color: #fff;
  display: flex;
}
#system .container .slider {
  width: 24%;
  background-color: #F4F6F8;
  min-height: 84vh;
}
#system .container .slider .search {
  text-align: center;
  padding: 30px 0;
}
#system .container .slider .search .ant-input-search {
  margin: 0 auto;
}
#system .container .slider .search .ant-input-search .ant-input:not(:last-child) {
  padding-right: 0;
  padding-left: 30px;
}
#system .container .slider .search .ant-input-search .ant-input-suffix {
  right: auto;
  left: 10px;
}
#system .container .slider .title {
  padding: 12px 33px;
  box-sizing: border-box;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  color: #fff;
  background-color: #F1948E;
  font-size: 16px;
}
#system .container .content {
  padding: 40px 66px;
  box-sizing: border-box;
  flex: 1;
  position: relative;
  padding-bottom: 120px;
}
#system .container .content .addBox {
  background-color: #ECEFF4;
  padding: 8px 20px;
  position: relative;
}
#system .container .content .addBox span {
  background: #f06e65;
  border: 1px solid #e94035;
  padding: 4px 12px;
  color: #fff;
  display: inline-block;
  cursor: pointer;
}
#system .container .content .addBox .edit {
  position: absolute;
  right: 10px;
  top: 50%;
  transform: translateY(-50%);
  height: 34px;
  line-height: 34px;
  color: #333;
  padding: 0 12px;
  cursor: pointer;
  background: #ffffff;
  border: 1px solid #c3c5ce;
}
#system .container .content .listBox {
  padding-top: 40px;
}
#system .container .content .listBox .list {
  background-color: #F3F6FB;
  padding: 10px 0 10px 45px;
  box-sizing: border-box;
}
#system .container .content .listBox .list .flex > span {
  position: relative;
  padding-right: 30px;
  display: inline-block;
}
#system .container .content .listBox .list .flex > span .delete {
  position: absolute;
  right: 10px;
  top: -2%;
  color: #999;
  font-size: 10px;
  cursor: pointer;
}
#system .container .content .listBox .list .flex > span .hide {
  display: none;
}
#system .container .content .listBox .list:nth-child(2n) {
  background-color: #fff;
}
#system .container .content .pagination {
  display: flex;
  padding: 10px 0;
  align-content: center;
  justify-content: center;
  position: absolute;
  bottom: 20px;
  width: 100%;
  left: 0;
}
#system .container .content .pagination span {
  display: inline-block;
  padding: 8px;
  cursor: pointer;
}
#system .flexBox {
  display: flex;
}
#system .flexBox .flex {
  width: 33.33%;
}
.add .btn {
  padding-top: 60px;
  text-align: center;
}
.add .btn span {
  display: inline-block;
  cursor: pointer;
  width: 100px;
  height: 40px;
  line-height: 38px;
  font-size: 20px;
  border-radius: 2px;
  border: 1px solid #979797;
  margin-left: 30px;
}
.add .btn .active {
  background: #f06e65;
  border-radius: 2px;
  border: 1px solid #ee6258;
  color: #fff;
}
